import st from "./Paginator.module.css";
import React, {useEffect, useState} from "react";
import {NavLink, useNavigate} from "react-router-dom";


const Paginator = ({pagesCount, setCurrentPage, currentPage, isFetching}) => {
    return (
        <div className={st.paginator_wrapper}>

            {(currentPage > 2)
                ? <CreatePageButton isBlocked={isFetching} pageNum={1} currentPage={currentPage} setCurrentPage={setCurrentPage}/>
                : <div></div>
            }

            <div>
                {(currentPage > 2) && <span>...</span>}
            </div>

            {(currentPage > 1)
                ? <CreatePageButton isBlocked={isFetching} pageNum={currentPage - 1} currentPage={currentPage} setCurrentPage={setCurrentPage}/>
                : <div></div>
            }
            <CreatePageButton isBlocked={isFetching} pageNum={currentPage} currentPage={currentPage} setCurrentPage={setCurrentPage}/>

            {(currentPage < pagesCount)
                ? <CreatePageButton isBlocked={isFetching} pageNum={+currentPage + 1} currentPage={currentPage} setCurrentPage={setCurrentPage}/>
                : <div></div>
            }
            <div>
                {(currentPage < (pagesCount - 1)) && <span>...</span>}
            </div>

            {(currentPage < pagesCount - 1)
                ? <CreatePageButton isBlocked={isFetching} pageNum={pagesCount} currentPage={currentPage} setCurrentPage={setCurrentPage}/>
                : <div></div>
            }
        </div>
    )
}
export default Paginator


const CreatePageButton = ({pageNum, currentPage, setCurrentPage, isBlocked, setBlocked}) => {
    let activeLink = st.current_page + " " + st.usual_btn
    let usualLink = st.usual_btn
    const navigate = useNavigate()
    const handleClick = (e) => {
        e.preventDefault()
        navigate(`/users/${pageNum}`)
    }
    return <div onClick={() => {setCurrentPage(pageNum)}}>
        <button onClick={isBlocked ? null : handleClick}
                disabled={isBlocked}
                className={(pageNum === currentPage) ? activeLink : usualLink}
        >
            {pageNum}
        </button>
    </div>
}
